class Storage {
    static TOKEN_KEY = 'token_key';
    constructor() {
        this.container = window.sessionStorage || window.localStorage;
    }
    set(key, value) {
        this.container.setItem(key, value);
    }
    get(key) {
        return this.container.getItem(key);
    }
}
const storage = new Storage();
export {
    storage,
    Storage
};